import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Todos from './components/Todos';
import Header from './components/layout/Header';
import AddTodo from './components/AddTodo';
import About from './components/pages/About';
// import uuid from 'uuid';
import axios from 'axios';
import './App.css';

class App extends Component {
  state = {
    todos: []
  }
  componentDidMount() {
    axios.get('https://jsonplaceholder.typicode.com/todos?_limit=5').then(res => this.setState({ todos: res.data }))
  }

  //State Change Toggle 
  toggleComplete = (id) => {
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id === id) {
          todo.completed = !todo.completed
        }
        return todo;
      })
    });
  }
  //Delete Todo
  delTodo = (id) => {
    axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`).then(res => this.setState({
      todos: [...this.state.todos.filter((todo) => (todo.id !== id))]
    }));
  }
  //Add todo
  AddTodo = (title) => {
    axios.post('https://jsonplaceholder.typicode.com/todos', {
      title: title,
      completed: false
    }).then(res => this.setState({
      todos: [...this.state.todos, res.data]
    }));
  }
  render() {
    return (
      <Router>
        <div className="App">
          <Header />
          <div className="innerContainer">
            {/* Home Route */}
            <Route exact path='/' render={props => (
              <React.Fragment>
                <AddTodo AddTodo={this.AddTodo} />
                <Todos todos={this.state.todos} toggleComplete={this.toggleComplete} delTodo={this.delTodo} />
              </React.Fragment>
            )} />
            {/* About Route */}
            <Route path='/about' component={About} />
          </div>
        </div>
      </Router>

    );
  }
}
//need to migrate nav bar to header compnent
export default App;
